import { GlobalDirectoryBean } from '../global-directory-model/bean/global-directory-model.bean';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { Message, TreeNode } from 'primeng/primeng';
import { Component, OnInit, Input, Output, EventEmitter, OnChanges, ElementRef, AfterViewInit } from '@angular/core';
import { SelectItem } from 'primeng/components/common/selectitem';
import { Commfunc } from '../../../../../../@uisftech/common/service/commonFun';
import { ProductManageHttpService } from '../../../http/product-manage.http.service';

@Component({
    // tslint:disable-next-line:component-selector
    selector: 'sale-product-config-col',
    templateUrl: './sale-product-config-copy.component.html',
    styleUrls: ['./sale-product-config-copy.component.scss'],
    providers: [ProductManageHttpService, Commfunc]
})

export class SaleProductConfigCopyComponent implements OnInit, AfterViewInit {
    productIdRow: any;
    pageNum1: number = 1;
    pageSize1: number = 10;
    productId: any;
    checkDate: any;
    first: number = 0;
    total: any;
    ANSG: any;
    @Input() inValue: string;
    @Output() outValue: EventEmitter<any> = new EventEmitter();
    // tslint:disable-next-line:no-inferrable-types
    head_title: string = '全局目录';
    // 提示信息
    msgs: Message[] = [];
    // 列表
    cars_2: any;
    treeData: any;
    prodId: any;
    prodName: any;
    listData: any = {};
    listData1: any = {};
    productTypeId: SelectItem[];
    // 模态框
    showModel: string;
    headerTitle: string;
    // tslint:disable-next-line:no-inferrable-types
    display: boolean = false;
    query: boolean = false;
    // 组件的参数
    updateValue: any = [];
    globalDirectoryBean: GlobalDirectoryBean = new GlobalDirectoryBean();

    // 分页
    pageSize: number; // 每页显示数量
    pageNum: number; // 当前页
    code: any;
    jichuArr: any = [];
    constructor(private productManageHttpService: ProductManageHttpService,
        private commfunc: Commfunc, private route: ActivatedRoute,
        public element: ElementRef) {
        this.codeValues(); // 调用方法，获取全部码值
        this.productTypeId = this.code['ProductTypeId0'];
    }
    ngOnInit() {
        // this.productTypeId = [
        //     { label: '可售产品', value: 'AvailableProduct' },
        //     { label: '基础产品', value: 'T' },
        // ];
        this.query_line();
    }
    // 码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret; // code是自己声明的变量
    }
    // tslint:disable-next-line:use-life-cycle-interface
    ngOnChanges() {

    }

    ngAfterViewInit() {
        // 隐藏tree内部自带的滚动条
        // this.element.nativeElement.querySelector('.ui-tree-container').style.overflow = 'unset';
    }
    selceteRow(item) {
        this.productIdRow = item.data.productId;
    }
    DblclickRow(item) {
        const params = {
            productId: item.data.productId
        };
        this.productManageHttpService.getAvailableProductTZB(params).subscribe(data => {
            if (data.returnCode.code === 'success') {
                this.listData = data;
                this.listData1 = data.availableProduct;
                const parmes = {
                    display: false,
                    depart: this.listData,
                };
                this.outValue.emit(parmes);
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        });
    }
    query_line() {
        this.productManageHttpService.productCategoryLineQueryList(this.globalDirectoryBean).subscribe(data => {
            if (data.returnCode.code === 'success') {
                const tempArr = [];
                // console.log(data.productLineList);
                data.productLineList.forEach(item => {
                    const tempObj = { id: 'line' };
                    tempObj['label'] = item['productLineName'];
                    tempObj['value'] = item;
                    tempObj['children'] = [{}];
                    tempArr.push(tempObj);
                });
                this.treeData = tempArr;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        });
    }

    nodeCheck(event) {
        this.query = false;
        this.productIdRow = '';
        this.prodId = '';
        this.prodName = '';
        if (event.node.id === 'line') {
            this.listData1['productId'] = event.node.value.productLineId;
            // 清除产品组基础产品ID
            this.listData1['lineId'] = event.node.value.productLineId;
            this.listData1['groupId'] = '';
            this.listData1['jichuId'] = '';
            this.listData1['productName'] = event.node.value.productLineName;
            this.listData1['productTypeId'] = '产品线';
            this.listData1['description'] = event.node.value.productLineStatusName;
        }
        if (event.node.id === 'group') {
            this.listData1['productId'] = event.node.value.groupId;
            this.listData1['lineId'] = event.node.value.productLineId;
            // 清除基础产品ID
            this.listData1['groupId'] = event.node.value.groupId;
            this.listData1['jichuId'] = '';
            this.listData1['productName'] = event.node.value.groupName;
            this.listData1['productTypeId'] = '产品组';
            this.listData1['description'] = event.node.value.groupDsc;
        }
        if (event.node.id === 'jichu') {
            this.listData1['productId'] = event.node.value.tplId;
            this.listData1['lineId'] = event.node.parent.value.productLineId;
            this.listData1['groupId'] = event.node.value.parentId;
            this.listData1['jichuId'] = event.node.value.tplId;
            this.listData1['productName'] = event.node.value.tplName;
            this.listData1['productTypeId'] = '基础产品';
            this.listData1['description'] = event.node.value.tplDsc;
        }
        if (event.node.data === -1) {
            this.pageNum++;
            this.jichuArr.splice(this.jichuArr.length - 1, 1);
            const param = {
                productTemplateId: event.node.parent.value.tplId,
                pageSize: this.pageSize = 20,
                pageNum: this.pageNum
            };
            this.productManageHttpService.findAvailableProductList(param).subscribe(data => {
                if (data.returnCode.code === 'success') {
                    data.resultList.forEach(item => {
                        const tempObj = { id: 'keshou' };
                        tempObj['label'] = item['productName'];
                        tempObj['value'] = item;
                        tempObj['children'] = [{}];
                        this.jichuArr.push(tempObj);
                    });
                    if (data.resultList.length === 20) {
                        const tempObj1 = {};
                        tempObj1['label'] = '...';
                        tempObj1['data'] = -1;
                        this.jichuArr.push(tempObj1);
                    }
                    event.node.children = this.jichuArr;
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
            });
        }
        if (event.node.id === 'keshou') {
            const params = {
                productId: event.node.value.productId
            };
            this.productManageHttpService.getAvailableProductTZB(params).subscribe(data => {
                if (data.returnCode.code === 'success') {
                    this.listData = data;
                    this.listData1 = data.availableProduct;
                    this.listData1['productTypeId'] = '可售产品';
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
            });
        }
    }
    // +号点击
    nodeExpend(event) {
        if (event.node.id === 'line') {
            this.globalDirectoryBean.productLineId = event.node.value.productLineId;
            const arr = [];
            this.productManageHttpService.productCategoryGroupInfoQueryList(this.globalDirectoryBean).subscribe(data => {
                if (data.returnCode.code === 'success') {
                    data.productGroupInfoList.forEach(item => {
                        const tempObj = { id: 'group' };
                        tempObj['label'] = item['groupName'];
                        tempObj['value'] = item;
                        tempObj['children'] = [{}];
                        tempObj['value']['productLineId'] = this.globalDirectoryBean.productLineId;
                        tempObj['value']['groupId'] = item['groupId'];
                        tempObj['value']['groupName'] = item['groupName'];
                        tempObj['value']['groupDsc'] = item['description'];
                        arr.push(tempObj);
                    });
                    event.node.children = arr;
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
            });
        }
        if (event.node.id === 'group') {
            const param = {
                groupId: event.node.value.groupId,
                // statusFlag: 'Y' // 只查询基础产品
            };
            const arr = [];
            this.productManageHttpService.pdGroupTemplateQuery(param).subscribe(data => {
                if (data.returnCode.code === 'success') {
                    data.groupTempList.forEach(item => {
                        const tempObj = { id: 'jichu' };
                        tempObj['label'] = item.tplName;
                        tempObj['value'] = item;
                        tempObj['children'] = [{}];
                        arr.push(tempObj);
                    });
                    event.node.children = arr;
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
            });
        }
        if (event.node.id === 'jichu') {
            const param = {
                productTemplateId: event.node.value.tplId,
                pageSize: this.pageSize = 20,
                pageNum: this.pageNum = 1
            };
            this.jichuArr = [];
            this.productManageHttpService.findAvailableProductList(param).subscribe(data => {
                if (data.returnCode.code === 'success') {
                    data.resultList.forEach(item => {
                        const tempObj = { id: 'keshou' };
                        tempObj['label'] = item['productName'];
                        tempObj['value'] = item;
                        tempObj['children'] = [];
                        this.jichuArr.push(tempObj);
                    });
                    if (data.resultList.length === 20) {
                        const tempObj1 = {};
                        tempObj1['label'] = '...';
                        tempObj1['data'] = -1;
                        this.jichuArr.push(tempObj1);
                    }
                    event.node.children = this.jichuArr;
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
            });
        }
        if (event.node.id === 'keshou') {
            const arr = [];
            event.node.children = arr;
        }
    }
    queryClick() {
        this.query = true;
        const params = {
            productId: this.prodId,
            productName: this.prodName,
            pageSize: this.pageSize1,
            pageNum: this.pageNum1,
            productLineId: this.listData1['lineId'], // 产品线ID
            productGroupId: this.listData1['groupId'], // 产品组id
            productTemplateId: this.listData1['jichuId']
        };
        this.productManageHttpService.findAvailableProductList(params).subscribe(data => {
            if (data.returnCode.code === 'success') {
                this.cars_2 = data.resultList;
                this.total = data.total;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        });
    }
    paginate(event) {
        //每页显示的条数
        this.pageSize1 = event.rows;
        //当前页
        let currentPage = event.page + 1;
        this.pageNum1 = currentPage;
        //调用查询的方法
        this.first = (currentPage - 1) * this.pageSize1;
        this.queryClick();
    }
    queryPrduct(event) {
        const params = {
            productId: event
        };
        this.productManageHttpService.getAvailableProductTZB(params).subscribe(data => {
            if (data.returnCode.code === 'success') {
                this.listData = data;
                this.listData1 = data.availableProduct;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        });
    }
    serve() {
        // 是否审批
        // let paramse = {
        //     productId: this.listData1.productTemplateId,
        //     flag: 'Y',
        // };
        // this.productManageHttpService.productWorkFlowCheck(paramse).subscribe(data => {
        //     if (data.returnCode.code === 'success') {
        //         if (data.workFlowFlag == 'Y' || data.productFlag == 'Y' || data.productStatusFlag == 'Y') {
        //             this.msgs = [];
        //             this.msgs.push({ severity: 'info', summary: '提示', detail: '此基础产品已有工作流或已被拒绝，无法引用！' });
        //         } else {
        //             //引用
        //             let parmes = {
        //                 display: false,
        //                 depart: this.listData,
        //             }
        //             this.outValue.emit(parmes);
        //         }
        //     } else {
        //         this.msgs = [];
        //         this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
        //     }
        // }, error => {
        //     this.msgs = [];
        //     this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务错误' });
        // })
        if (this.productIdRow) {
            const params = {
                productId: this.productIdRow
            };
            this.productManageHttpService.getAvailableProductTZB(params).subscribe(data => {
                if (data.returnCode.code === 'success') {
                    this.listData = data;
                    this.listData1 = data.availableProduct;
                    const parmes = {
                        display: false,
                        depart: this.listData,
                    };
                    this.outValue.emit(parmes);
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
            });
        } else {
            if (this.listData.availableProduct) {
                const parmes = {
                    display: false,
                    depart: this.listData,
                };
                this.outValue.emit(parmes);
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择可售产品！' });
            }
        }
    }
}
