import { ProductManageHttpService } from "./../../../http/product-manage.http.service";
import { ActivatedRoute, ParamMap } from '@angular/router';
import { Message, TreeNode } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { checkNumber } from 'app/@uisftech/common/service/validator';
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config';
@Component({
    selector: 'sale-product-modify-website',
    templateUrl: './sale-product-modify-website.component.html',
    styleUrls: ['./sale-product-modify-website.component.scss'],
    providers: [ProductManageHttpService, Commfunc]
})

export class SaleProductModifyWebsiteComponent implements OnInit {
    @Input() inValue: any = [];
    @Output() outValue: EventEmitter<any> = new EventEmitter();
    //提示信息
    msgs: Message[] = [];
    tabMesg: any = tableMessage;//列表无数据
    special_attr: any = [];
    outValues: any = [];
    website: any;
    constructor(public productManageHttpService: ProductManageHttpService, public commfunc: Commfunc, public route: ActivatedRoute) { }
    ngOnInit() {
        this.queryname();
    }
    ngOnChanges() {

    }
    getType(obj) {
        var toString = Object.prototype.toString;
        var map = {
            '[object Boolean]': 'boolean',
            '[object Number]': 'number',
            '[object String]': 'string',
            '[object Function]': 'function',
            '[object Array]': 'array',
            '[object Date]': 'date',
            '[object RegExp]': 'regExp',
            '[object Undefined]': 'undefined',
            '[object Null]': 'null',
            '[object Object]': 'object'
        };
        if (obj instanceof Element) {
            return 'element';
        }
        return map[toString.call(obj)]
    }
    deepClone(data) {
        let type = this.getType(data);
        let obj;
        if (type === 'array') {
            obj = [];
        } else if (type === 'object') {
            obj = {};
        } else {
            return data;
        }
        if (type === 'array') {
            for (var i = 0, len = data.length; i < len; i++) {
                obj.push(this.deepClone(data[i]));
            }
        } else if (type === 'object') {
            for (var key in data) {
                obj[key] = this.deepClone(data[key]);
            }
        }
        return obj;
    }
    queryname() {
        this.productManageHttpService.queryChannel({}).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.inValue.forEach(item => {
                    data.channelList.forEach(key => {
                        if (item.channelId == key.value) {
                            this.special_attr.push({ channelId: item.channelId, siteName: key.label });
                            this.special_attr = this.deepClone(this.special_attr);
                        }
                    });
                });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
        // this.inValue.forEach(item => {
        //     let param = { webSiteId: item.channelId };
        //     this.productManageHttpService.webSiteDetail(param).subscribe(data => {
        //         if (data.returnCode.code == "success") {
        //             this.special_attr.push({ channelId: item.channelId, siteName: data.siteName });
        //             this.special_attr = this.deepClone(this.special_attr);
        //         } else {
        //             this.msgs = [];
        //             this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
        //         }
        //     }, error => {
        //         this.msgs = [];
        //         this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        //     })
        // });
    }
    websitec() {
        if (this.website == undefined) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '未选择站点！' });
        } else {
            this.inValue.forEach(itemse => {
                if (itemse.channelId == this.special_attr[this.website].channelId) {
                    let paramss = {}
                    this.outValues.push({
                        'fileList': itemse.fileList,
                        'imageList': itemse.imageList,
                        'infoList': itemse.infoList
                    });
                    paramss = {
                        display: false,
                        outed: this.outValues,
                    }
                    this.outValue.emit(paramss);
                }
            });
        }
    }
    // webcanel() {
    //     let paramss = {
    //         display: false
    //     }
    //     this.outValue.emit(paramss);
    // }
}