import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { Message } from 'primeng/primeng';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { ProductManageHttpService } from '../../../http/product-manage.http.service';
import { Commfunc } from '../../../../../../@uisftech/common/service/commonFun';

@Component({
  selector: 'app-display-properties-details',
  templateUrl: './display-properties-details.component.html',
  styleUrls: ['./display-properties-details.component.scss']
})
export class DisplaypropertiesDetailsComponent implements OnInit, OnChanges {
  tabMesg: any = tableMessage;//列表无数据
  @Input() inValue: any;

  @Output() outValue: EventEmitter<any> = new EventEmitter();
  cars: any = [];
  //模态框
  showModel: string;
  display: boolean = false;
  //模态框的标题
  headerTitle: string
  updateValue: any = [];
  //提示信息
  msgs: Message[] = [];

  list: any = [];
  productFeatureCategoryId: string;
  listValue: any = [];
  constructor(
    public productManageHttpService: ProductManageHttpService,
    private commfunc: Commfunc, ) { }
  //分页
  pageSize: number = 10;
  pageNum: number = 1;
  total: number;
  listValueList: any = [];
  ngOnInit() {
  }

  ngOnChanges() {
    this.queryEvent();
  }
  // 关闭
  closeClick() {
    this.outValue.emit(false);
  }
  queryEvent() {
    this.list = this.inValue;
    let param = {
      productId: this.inValue.productId,
      productAttrConfigId: this.inValue.productAttrConfigId,
      channelId: this.inValue.channelId,
      productFeatureCategoryId: this.inValue.productFeatureCategoryId,
      pageSize: this.pageSize,
      pageNum: this.pageNum
    };
    this.productManageHttpService.queryProductAttrConfigDetail(param).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.list.createdByUserLogin = data.createdByUserLogin;
        this.list.lastModifiedByUserLogin = data.lastModifiedByUserLogin;
        let createdStamp = new Date(data.createdStamp);
        this.list.createdStamp = this.commfunc.transDateN(createdStamp);
        let lastUpdatedStamp = new Date(data.lastUpdatedStamp);
        this.list.lastUpdatedStamp = this.commfunc.transDateN(lastUpdatedStamp);
        // this.listValue = data.resultList;
        // this.total = data.total;
        this.listValueList = data.resultList;
        if (data.resultList.length > 0) {
          this.total = data.resultList.length;
        } else {
          this.total = 0;
        }
        let currentPages1 = this.pageNum * 10;;
        if (this.listValueList.length > currentPages1) {
          let currentPages2 = currentPages1 - 10;
          this.listValue = this.listValueList.slice(currentPages2, currentPages1);
        } else {
          let currentPages2 = currentPages1 - 10;
          this.listValue = this.listValueList.slice(currentPages2);
        }
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    });
  }
  //分页
  paginate(event) {
    //每页显示的条数
    this.pageSize = event.rows;
    //当前页
    let currentPage = event.page + 1;
    this.pageNum = currentPage;
    //调用查询的方法
    this.queryEvent();
    // this.pageNum = 1;
  }
}
