    // 组件右分页
    paginates(event) {
        // 每页显示的条数
        this.pageSizes = event.rows;
        // 当前页
        let currentPages = event.page + 1;
        let currentPages1 = currentPages * 10;
        // 调用查询的方法
        if (this.funList_rightList) {
            this.totals = this.funList_rightList.length;
        } else {
            this.totals = 0;
        }
        this.funList_right = [];
        if (this.funList_rightList.length > currentPages1) {
            let currentPages2 = currentPages1 - 10;
            this.funList_right = this.funList_rightList.slice(currentPages2, currentPages1);
        } else {
            let currentPages2 = currentPages1 - 10;
            this.funList_right = this.funList_rightList.slice(currentPages2);
        }
        this.rightListFun = [];
    }
    //功能右移
    rightFunMovre() {
        let temps = [];
        let temps1 = [];
        let arrsright = [];
        this.leftListFun.forEach((item, i) => {
            arrsright.push(item)
            arrsright.sort(function (a, b) { return b - a; })
            this.funList_right.push(this.funList_left[item])
            this.funList_rightList.push(this.funList_left[item]);
        });
        arrsright.forEach((item, i) => {
            this.funList_left.splice(item, 1)
        });
        temps = _.clone(this.funList_right)
        this.funList_right = _.clone(temps)
        this.funList_rightList = _.clone(this.funList_rightList);
        temps1 = _.clone(this.funList_left)
        this.funList_left = _.clone(temps1)
        this.leftListFun = []
        this.leftListFunAll = false;
        if (this.funList_rightList) {
            this.totals = this.funList_rightList.length;
        } else {
            this.totals = 0;
        } if (this.funList_left) {
            this.total = this.funList_left.length;
        } else {
            this.total = 0;
        }
    }
    //功能左移
    liftFunMovre() {
        let tempss = [];
        let tempss1 = [];
        let arrsleft = [];
        this.rightListFun.forEach(item => {
            arrsleft.push(item)
            arrsleft.sort(function (a, b) { return b - a; })
            this.funList_left.push(this.funList_right[item])
        });
        arrsleft.forEach((item, i) => {
            this.funList_rightList.forEach((items, k) => {
                if (items.productFunctionName === this.funList_right[item].productFunctionName) {
                    this.funList_rightList.splice(k, 1);
                }
            });
            this.funList_right.splice(item, 1)
        });
        tempss = _.clone(this.funList_right)
        this.funList_right = _.clone(tempss)
        this.funList_rightList = _.clone(this.funList_rightList);
        tempss1 = _.clone(this.funList_left)
        this.funList_left = _.clone(tempss1)
        this.rightListFun = []
        if (this.funList_rightList) {
            this.totals = this.funList_rightList.length;
        } else {
            this.totals = 0;
        }
        if (this.funList_left) {
            this.total = this.funList_left.length;
        } else {
            this.total = 0;
        }
        this.rightListFunAll = false;
    }

