import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Params, ParamMap } from '@angular/router';
import { ProductManageHttpService } from '../../http/product-manage.http.service';
import { Message } from 'primeng/primeng';
import { Commfunc } from '../../../../../@uisftech/common/service/commonFun';
import * as _ from 'lodash';

@Component({
    selector: 'app-examine',
    templateUrl: './examine.component.html',
    styleUrls: ['./examine.component.scss'],
    providers: [ProductManageHttpService]
})
export class ExamineComponent implements OnInit {
    select_2: any;
    select_1: any;
    paymentMethodTypeList: any = [];
    productFunctionList: any = [];
    productFeatureCategoryupdate: any = [];
    updateproductFeatureCategoryList: any = [];
    showdog: boolean;
    questionListdelete: any = [];
    batchlist: any = [];
    questionListadd: any = [];
    contentListdelete: any = [];
    contentListadd: any = [];
    deletepaymentMethodTypeList: any = [];
    addpaymentMethodTypeList: any = [];
    deleteproductFunctionListbase: any = [];
    addproductFunctionListbase: any = [];
    productFeatureCategorydelete: any = [];
    productFeatureCategoryadd: any = [];
    productGroupName: any;
    productLineName: any;
    categoryName: any;
    productCategory: any;
    addproductMsgs: any;
    deleteproductMsgs: any;
    deleteproductFunctionList: any;
    addproductFunctionList: any;
    description: any;
    configLevel: any;
    code: any;
    deleteproductFeatureCategoryList: any = [];
    addproductFeatureCategoryList: any = [];
    productFeatureCategoryList: any;
    oldsales: string;
    newsales: string;
    oldmanage: string;
    newmanage: string;
    oldmarket: string;
    newmarket: string;
    salesPostList: any;
    productManagerList: any;
    manageDepartmentList: any;
    marketDepartmentList: any;
    releaseDate: any;
    introductionDate: any;
    businessCode: any;
    subBusinessCode: any;
    inOutTableMode: any;
    currency: any;
    productName: any;
    crmProductTreeCode: any;
    availableProduct: any;
    list: any;
    varMap: any = [];
    showModel: string;
    updateValue: any;
    headerTitle: string;
    sale: any = [];
    base: any = [];
    //提示信息
    msgs: Message[] = [];
    productId: any;
    display: boolean = false;
    display2: boolean = false;
    shows: string;// 点击功能
    vlaueOpenf: string = 'open';
    surveydelete: any = [];
    surveyadd: any = [];
    isMybankCust: any = [];// 是否必输
    isMybank: any = [];
    QuestionLists: any = [];// 属性值类型
    constructor(
        public route: ActivatedRoute,
        public productManageHttpService: ProductManageHttpService,
        public commfunc: Commfunc,
    ) {
        if (this.route) {
            console.log(this.route.snapshot.params);
            this.productId = this.route.snapshot.params['baseProductId'];
            // this.varMap = JSON.parse(this.route.snapshot.params.varMap);
            this.list = this.route.snapshot.params;
            this.varMap = JSON.parse(this.commfunc.getSessionDataCH('product-examine'));
        }
        this.codeValues(); //调用方法，获取全部码值
        this.configLevel = this.code['configLevel'];
        this.select_1 = this.code['select_1']; // 币种
        this.select_2 = this.code['select_2']; // 表内表外
        this.isMybank = this.code['isMybankCust'];
        this.isMybankCust = this.isMybank.splice(1);
    }

    ngOnInit() {
        this.queryQuestionType();
        this.query();
        this.UpdateQuery();
        this.batchlist = {
            applySerialNo: this.varMap.applySerialNo,
        };
    }

    // 信息查询
    query() {
        let param = { baseProductId: this.varMap.productId };
        if (this.varMap.businessType == 'productTemplateAdd' || this.varMap.businessType == 'productTemplateUpdate' || this.varMap.businessType == 'productTemplateDelete') {
            // 基础
            this.productManageHttpService.getBaseProductDetails(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.base = data.productCategory; // 详情展示
                    let created = new Date(this.base.createdDate);
                    this.base.createdDate = this.transDate(created);
                    let lastModified = new Date(this.base.lastModifiedDate);
                    this.base.lastModifiedDate = this.transDate(lastModified);
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
                }
            });
        } else if (this.varMap.businessType == 'productAvailableAdd' || this.varMap.businessType == 'productAvailableUpdate' || this.varMap.businessType == 'productAvailableDelete') {
            // 可售
            let param1 = { productId: this.varMap.productId };
            this.productManageHttpService.getAvailableProductTZB(param1).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.sale = data.availableProduct; // 详情展示
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
                }
            });
        }
    }
    // 详情
    Modify() {
        this.display = true;
        this.updateValue = this.varMap.productId;
        if (this.varMap.businessType == 'productTemplateAdd' || this.varMap.businessType == 'productTemplateUpdate' || this.varMap.businessType == 'productTemplateDelete') {
            this.headerTitle = '基础产品详情';
            this.showModel = '1';
        } else if (this.varMap.businessType == 'productAvailableAdd' || this.varMap.businessType == 'productAvailableUpdate' || this.varMap.businessType == 'productAvailableDelete') {
            this.headerTitle = '可售产品详情';
            this.showModel = '2';
        }
    }
    // 日期格式转换
    transDate(value) {
        if (!value) {
            return ''
        }
        let time = new Date(value);
        let year = value.getFullYear();
        let month = value.getMonth() + 1 + '';
        let data = value.getDate() + '';
        let h = value.getHours() + '';
        let m = value.getMinutes() + '';
        let s = value.getSeconds() + '';
        if (month.length < 2) {
            month = '0' + month;
        }
        if (data.length < 2) {
            data = '0' + data;
        }
        if (h.length < 2) {
            h = '0' + h;
        }
        if (m.length < 2) {
            m = '0' + m;
        }
        if (s.length < 2) {
            s = '0' + s;
        }
        let newtime = year + '-' + month + '-' + data + '  ' + h + ':' + m + ':' + s;
        return newtime;
    }
    // 修改审批接口调用
    UpdateQuery() {
        let param = { productId: this.varMap.productId };
        console.log(param);
        if (this.varMap.businessType == 'productTemplateUpdate') {
            this.productManageHttpService.getBasicProductComparison(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.categoryName = data.productCategory.categoryName;
                    this.productCategory = data.productCategory;
                    this.productFunctionList = data.productFunctionList;
                    this.description = data.productCategory.description;
                    this.productLineName = data.productCategory.productLineName;
                    this.productGroupName = data.productCategory.productGroupName;
                    this.productFeatureCategoryList = data.productFeatureCategoryList;
                    this.paymentMethodTypeList = data.productContentList;
                    this.resultfClick(0, this.paymentMethodTypeList[0], 'pay');
                    // 组件信息
                    if (data.productFeatureCategoryList) {
                        if (data.productFeatureCategoryList.length > 0) {
                            data.productFeatureCategoryList.forEach(item => {
                                if (item.dataType == 'add') {
                                    this.productFeatureCategoryadd.push(item);
                                } else if (item.dataType == 'delete') {
                                    this.productFeatureCategorydelete.push(item);
                                } else if (item.dataType == 'update') {
                                    this.productFeatureCategoryupdate.push(item);
                                }
                            });
                        }
                    }
                    this.addproductFunctionListbase = data.productFunctionList.addList;
                    this.deleteproductFunctionListbase = data.productFunctionList.delList;
                    // this.paymentMethodTypeList = data.paymentMethodTypeList;
                    // this.addpaymentMethodTypeList = data.paymentMethodTypeList.addList;
                    // this.deletepaymentMethodTypeList = data.paymentMethodTypeList.delList;
                    if (data.contentList && data.contentList.length > 0) {
                        data.contentList.forEach(item => {
                            if (item.dataType == 'add') {
                                this.contentListadd.push(item);
                            } else if (item.dataType == 'delete') {
                                this.contentListdelete.push(item);
                            }
                        });
                    }
                    if (data.questionList && data.questionList.length > 0) {
                        data.questionList.forEach(item => {
                            if (item.dataType == 'add') {
                                this.questionListadd.push(item);
                            } else if (item.dataType == 'delete') {
                                this.questionListdelete.push(item);
                            }
                        });
                    }
                }
            });
        }
        if (this.varMap.businessType == 'productAvailableUpdate') {
            this.productManageHttpService.getAvailableProductComparison(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.availableProduct = data.availableProduct;
                    this.productFeatureCategoryList = data.productFeatureCategoryList;
                    this.productFunctionList = data.productFunctionList;
                    let market = '', market2 = '', manage = '', manage2 = '', product = '', product2 = '', sales = '', sales2 = '';
                    if (this.availableProduct.crmProductTreeCode) {
                        if (!this.availableProduct.crmProductTreeCode.oldValue) {
                            this.availableProduct.crmProductTreeCode.oldValue = '无';
                        }
                    }
                    if (this.availableProduct.subBusinessCode) {
                        if (!this.availableProduct.subBusinessCode.oldValue) {
                            this.availableProduct.subBusinessCode.oldValue = '无';
                        }
                    }
                    if (this.availableProduct.businessCode) {
                        if (!this.availableProduct.businessCode.oldValue) {
                            this.availableProduct.businessCode.oldValue = '无';
                        }
                    }
                    this.productName = this.availableProduct.productName;
                    this.currency = this.availableProduct.currency;
                    this.inOutTableMode = this.availableProduct.inOutTableMode;
                    this.subBusinessCode = this.availableProduct.subBusinessCode;
                    this.businessCode = this.availableProduct.businessCode;
                    this.crmProductTreeCode = this.availableProduct.crmProductTreeCode;
                    this.releaseDate = this.availableProduct.releaseDate;
                    this.introductionDate = this.availableProduct.introductionDate;
                    this.description = this.availableProduct.description;
                    if (this.description) {
                        if (!this.description.oldValue) {
                            this.description.oldValue = '无';
                        }
                    }
                    this.marketDepartmentList = this.availableProduct.marketDepartmentList;
                    this.salesPostList = this.availableProduct.salesPostList;
                    this.manageDepartmentList = this.availableProduct.manageDepartmentList;
                    this.productManagerList = this.availableProduct.productManagerList;
                    if (this.marketDepartmentList) {
                        if (this.availableProduct.marketDepartmentList.addList) {
                            this.availableProduct.marketDepartmentList.addList.forEach(item => {
                                if (this.availableProduct.marketDepartmentList.addList.length > 1) {
                                    market += item.marketDepartmentName + ',';
                                } else {
                                    market = item.marketDepartmentName;
                                }
                            });
                        }
                    }
                    this.newmarket = market;
                    if (this.marketDepartmentList) {
                        if (this.availableProduct.marketDepartmentList.delList) {
                            this.availableProduct.marketDepartmentList.delList.forEach(item => {
                                if (this.availableProduct.marketDepartmentList.delList.length > 1) {
                                    market2 += item.marketDepartmentName + ',';
                                } else {
                                    market2 = item.marketDepartmentName;
                                }
                            });
                        }
                    }
                    this.oldmarket = market2;
                    if (this.manageDepartmentList) {
                        if (this.availableProduct.manageDepartmentList.addList) {
                            this.availableProduct.manageDepartmentList.addList.forEach(item => {
                                if (this.availableProduct.manageDepartmentList.addList.length > 1) {
                                    manage += item.manageDepartmentName + ',';
                                } else {
                                    manage = item.manageDepartmentName;
                                }
                            });
                        }
                    }
                    this.newmanage = manage;
                    if (this.manageDepartmentList) {
                        if (this.availableProduct.manageDepartmentList.delList) {
                            this.availableProduct.manageDepartmentList.delList.forEach(item => {
                                if (this.availableProduct.manageDepartmentList.delList.length > 1) {
                                    manage2 += item.manageDepartmentName + ',';
                                } else {
                                    manage2 = item.manageDepartmentName;
                                }
                            });
                        }
                    }
                    this.oldmanage = manage2;
                    if (this.productManagerList) {
                        if (this.availableProduct.productManagerList.addList) {
                            this.availableProduct.productManagerList.addList.forEach(item => {
                                if (this.availableProduct.productManagerList.addList.length > 1) {
                                    product += item.productManagerName + ',';
                                } else {
                                    product = item.productManagerName;
                                }
                            });
                        }
                    }
                    this.newmanage = product;
                    if (this.productManagerList) {
                        if (this.availableProduct.productManagerList.delList) {
                            this.availableProduct.productManagerList.delList.forEach(item => {
                                if (this.availableProduct.productManagerList.delList.length > 1) {
                                    product2 += item.productManagerName + ',';
                                } else {
                                    product2 = item.productManagerName;
                                }
                            });
                        }
                    }
                    this.oldmanage = product2;
                    if (this.salesPostList) {
                        if (this.availableProduct.salesPostList.addList) {
                            this.availableProduct.salesPostList.addList.forEach(item => {
                                if (this.availableProduct.salesPostList.addList.length > 1) {
                                    sales += item.salesPostName + ',';
                                } else {
                                    sales = item.salesPostName;
                                }
                            });
                        }
                    }
                    this.newsales = sales;
                    if (this.salesPostList) {
                        if (this.availableProduct.salesPostList.delList) {
                            this.availableProduct.salesPostList.delList.forEach(item => {
                                if (this.availableProduct.salesPostList.delList.length > 1) {
                                    sales2 += item.salesPostName + ',';
                                } else {
                                    sales2 = item.salesPostName;
                                }
                            });
                        }
                    }
                    this.oldsales = sales2;
                    // 组件信息
                    if (data.productFeatureCategoryList.length > 0) {
                        data.productFeatureCategoryList.forEach(item => {
                            if (item.dataType == 'add') {
                                this.addproductFeatureCategoryList.push(item);
                            } else if (item.dataType == 'delete') {
                                this.deleteproductFeatureCategoryList.push(item);
                            } else if (item.dataType == 'update') {
                                if (typeof item.min == 'string') { item['minType'] = false; } else {
                                    item['minType'] = true;
                                }
                                if (typeof item.max == 'string') { item['maxType'] = false; } else {
                                    item['maxType'] = true;
                                }
                                this.updateproductFeatureCategoryList.push(item);
                            }
                        });
                    }
                    if (data.productFunctionList) {
                        this.addproductFunctionList = data.productFunctionList.addList;
                        this.deleteproductFunctionList = data.productFunctionList.delList;
                    }
                    if (data.productMsgs) {
                        this.addproductMsgs = data.productMsgs.addList;
                        this.deleteproductMsgs = data.productMsgs.delList;
                    }
                }
            });
        }
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    // 差异对比
    difference() {
        this.display2 = true;
        this.headerTitle = '修改信息对比';
    }
    detailsavailable(param) {
        this.display = param;
    }
    detailsbase(param) {
        this.display = param;
    }
    //  点击组件属性查看
    resultfClick(i, col, item) {
        this.shows = col.functionName;
        if (col.paymentMethodType) {
            this.addpaymentMethodTypeList = col.paymentMethodType.addList;
            this.deletepaymentMethodTypeList = col.paymentMethodType.delList;
        } else {
            this.addpaymentMethodTypeList = [];
            this.deletepaymentMethodTypeList = [];
        }
        if (col.content) {
            this.contentListadd = col.content.addList;
            this.contentListdelete = col.content.delList;
        } else {
            this.contentListadd = [];
            this.contentListdelete = [];
        }
        if (col.surveyOption) {
            this.surveyadd = col.surveyOption.addList;
            this.surveydelete = col.surveyOption.delList;
        } else {
            this.surveyadd = [];
            this.surveydelete = [];
        }
    }
    // 是否展开功能框
    openClickf() {
        if (this.vlaueOpenf == 'open') {
            this.vlaueOpenf = 'down'
        } else {
            this.vlaueOpenf = 'open'
        }
    }
    // 属性值类型
    queryQuestionType() {
        this.productManageHttpService.queryQuestionType({}).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.QuestionLists = [];
                let obj = data.questionTypeMap;
                for (var key in obj) {
                    this.QuestionLists.push({
                        label: obj[key],
                        value: key,
                    });
                }
                this.QuestionLists = _.clone(this.QuestionLists);
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        })
    }
    // 按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id);
    }
}