                data.productFeatureCategoryList.forEach((item, i) => {
                    //  tslint:disable-next-line:max-line-length
                    speciall.push({ 'productFeatureCategoryName': item.productFeatureCategoryName, 'description': item.productFeatureCategoryName, 'productFeatureCategoryId': item.productFeatureCategoryId, 'productFeatureTypeList': item.productFeatureTypeList });
                });
                this.cars_4 = speciall;
                speciall = [];
                //  功能应用
                this.funList_right = data.productFunctionList;
                //  包装 产品描述
