    //  多行列表单选按钮
    checkBoxClick10(i, data) {
        let tempss = [];
        if (data.checkBox10) {
            this.select_atr.push(i);
            this.chooseData10.push(data);
        } else {
            // tslint:disable-next-line:no-shadowed-variable
            this.chooseData10.forEach((item, i) => {
                if (item === data) {
                    this.chooseData10.splice(i, 1);
                    this.select_atr.splice(i, 1);
                }
            });
        }
        let aka = 0;
        this.cars_attrbute.forEach(item => {
            if (!item.checkBox10) {
                aka++;
            }
        });
        if (aka == 0) {
            this.checkBoxAll10 = true;
        } else {
            this.checkBoxAll10 = false;
        }
        const arr0 = [];
        // tslint:disable-next-line:no-shadowed-variable
        for (let i = 0; i < this.select_atr.length; i++) {
            if (arr0.indexOf(this.select_atr[i]) === -1) {
                arr0.push(this.select_atr[i]);
            }
        }
        tempss = _.clone(arr0);
        this.select_atr = _.clone(tempss);
    }

    //  全选按钮
    checkAllClickA(item) {
        for (let i = 0; i < item.productFeatureList.length; i++) {
            if (item['checkDate'] === true) {
                item.productFeatureList[i].checkBox = true;
                this.chooseData.push(item.productFeatureList[i]);
            } else {
                this.chooseData = [];
                item.productFeatureList[i].checkBox = false;
            }
        }
    }
    //  单选按钮
    checkBoxClickA(i, data, item) {
        let isChooseBox = 0;
        if (data.checkBox) {
            this.chooseData.push(data);
        } else {
            this.chooseData.forEach((item1, j) => {
                if (item1 === data) {
                    this.chooseData.splice(j, 1);
                }
            });
        }
        item.productFeatureList.forEach(element => {
            if (element.checkBox) {
                isChooseBox++;
            }
        });
        if (isChooseBox === item.productFeatureList.length) {
            item['checkDate'] = true;
        } else {
            item['checkDate'] = false;
        }
    }

    //  功能右移
    rightFunMovre() {
        let temps = [];
        let temps1 = [];
        const arrsright = [];
        this.leftListFun.forEach((item, i) => {
            arrsright.push(item);
            arrsright.sort(function (a, b) { return b - a; });
            this.funList_right.push(this.funList_left[item]);
            this.funList_rightList.push(this.funList_left[item]);
        });
        arrsright.forEach((item, i) => {
            this.funList_left.splice(item, 1);
        });
        temps = _.clone(this.funList_right);
        this.funList_right = _.clone(temps);
        this.funList_rightList = _.clone(this.funList_rightList);
        temps1 = _.clone(this.funList_left);
        this.funList_left = _.clone(temps1);
        this.leftListFun = [];
        this.leftListFunAll = false;
        if (this.funList_rightList) {
            this.totals = this.funList_rightList.length;
        } else {
            this.totals = 0;
        } if (this.funList_left) {
            this.total = this.funList_left.length;
        } else {
            this.total = 0;
        }
    }
    //  功能左移
    liftFunMovre() {
        let tempss = [];
        let tempss1 = [];
        const arrsleft = [];
        this.rightListFun.forEach(item => {
            arrsleft.push(item);
            arrsleft.sort(function (a, b) { return b - a; });
            this.funList_left.push(this.funList_right[item]);
        });
        arrsleft.forEach((item, i) => {
            this.funList_rightList.forEach((items, k) => {
                if (items.productFunctionName === this.funList_right[item].productFunctionName) {
                    this.funList_rightList.splice(k, 1);
                }
            });
            this.funList_right.splice(item, 1);
        });
        tempss = _.clone(this.funList_right);
        this.funList_right = _.clone(tempss);
        this.funList_rightList = _.clone(this.funList_rightList);
        tempss1 = _.clone(this.funList_left);
        this.funList_left = _.clone(tempss1);
        this.rightListFun = [];
        if (this.leftListFun.length === this.funList_left.length && this.funList_left.length > 0) {
            this.leftListFunAll = true;
        } else {
            this.leftListFunAll = false;
        }
        this.rightListFunAll = false;
        if (this.funList_rightList) {
            this.totals = this.funList_rightList.length;
        } else {
            this.totals = 0;
        }
        if (this.funList_left) {
            this.total = this.funList_left.length;
        } else {
            this.total = 0;
        }
    }

