import { ModuleWithProviders } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
//页面
import { SaleProductViewComponent } from './sale-product-view/sale-product-view.component';//可售产品查看
import { SaleProductConfigComponent } from './sale-product-config/sale-product-config.component';//可售产品新增
import { SaleDetailsComponent } from './sale-product-view/sale-view-details/sale-view-details.component';//可售产品详情
import { SaleProductModifyComponent } from './sale-product-modify/sale-product-modify.component';//可售产品修改
import { DisplaypropertiesComponent } from './display-properties/display-properties.component';
import { DisplaypropertiesConfigComponent } from './display-properties-config/display-properties-config.component';
import { ExamineComponent } from './examine/examine.component';
const routes: Routes = [
    {
        path: 'sale-product-view', component: SaleProductViewComponent,
    },
    {
        path: 'sale-view-details', component: SaleDetailsComponent,
    },
    {
        path: 'sale-product-config', component: SaleProductConfigComponent,
    },
    {
        path: 'sale-product-modify', component: SaleProductModifyComponent,
    },
    {
        path: 'display-properties', component: DisplaypropertiesComponent,
    },
    {
        path: 'display-properties-config', component: DisplaypropertiesConfigComponent,
    },
    {
        path: 'examine', component: ExamineComponent,
    },
];

export const AvailableProductRouting: ModuleWithProviders = RouterModule.forChild(routes);
