import { Component, OnInit } from '@angular/core';
import { SelectItem, Message } from 'primeng/primeng';
import { ProductManageHttpService } from '../../http/product-manage.http.service';
import { PostsManageBean, ProductQBean } from '../bean/sale-product.bean'
import { ConfirmationService } from 'primeng/primeng';//删除
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { tableMessage } from 'app/@uisftech/common/constant/config';
@Component({
	selector: 'app-sale-product-view',
	templateUrl: './sale-product-view.component.html',
	styleUrls: ['./sale-product-view.component.scss'],
	providers: [ProductManageHttpService, ConfirmationService, Commfunc]
})

export class SaleProductViewComponent implements OnInit {
	saleProductType: any;;//可售产品-产品状态
	code: any;
	PostsManageBean: PostsManageBean = new PostsManageBean();
	productQBean: ProductQBean = new ProductQBean();

	//头部标题
	all_title: string = '可售产品查看';
	select_1: SelectItem[] = [];//默认下拉
	tabMesg: any = tableMessage;//列表无数据
	//组件的参数
	updateValue: any = [];
	//提示信息
	msgs: Message[] = [];
	//模态框
	showModel: string;
	headerTitle: string
	display: boolean = false;
	isdisabled: boolean = true;
	select_line: any = [];//产品线下拉
	select_group: any = [];//产品组下拉
	product_base_arr: any = [];//基础产品下拉
	line_value: any = [{
		productLineId: ''
	}];//产品线下拉值
	group_value: any = [{
		groupId: ''
	}];//产品组下拉值
	product_base_value: any = [];//基础产品
	//列表
	cars_2: any;
	//总页数
	total: string;

	//分页控制
	first: number = 0;
	constructor(
		public productManageHttpService: ProductManageHttpService,
		public confirmationService: ConfirmationService,
		public commfunc: Commfunc
	) {
		this.codeValues() //调用方法，获取全部码值
		this.saleProductType = this.code['fromStatusList'];
	}
	
	ngOnInit() {
		this.select_line = [
			{ label: '请选择', value: "" },
		];
		// this.saleProductType = [
		// 	{ label: '请选择', value: "" },
		// 	// { label: '已生效', value: "01" },
		// 	// { label: '待生效', value: "02" },
		// 	// { label: '升级中', value: "03" },
		// 	// { label: '待删除', value: "04" },
		// 	// { label: '待复核', value: "99" },
		// 	{ label: '待生效', value: "00" },
		// 	{ label: '待复核', value: "01" },
		// 	{ label: '已生效', value: "02" },
		// 	{ label: '待删除', value: "04" },
		// 	{ label: '升级中', value: "06" },
		// ];
		this.select_group = [
			{ label: '请先选择产品线', value: "" },
		];
		this.product_base_arr = [
			{ label: '请先选择产品组', value: "" },
		];
		this.select_1 = [
			{ label: '请选择', value: "" },
		];
		this.cars_2 = [
		]
		this.line_query();
		this.first = 0;
		this.searchList();
	}
	codeValues() {
		this.commfunc.codeValue();
		let ret = this.commfunc.codeDatas;
		return this.code = ret //code是自己声明的变量
	}
	//下拉菜单
	getDropdown() {

	}

	//查询始终第一页
	querySecle() {
		this.productQBean.pageSize = 10;
		this.productQBean.pageNum = 1;
		this.first = 0;
		this.searchList();
	}

	// 查询
	searchList() {
		this.productQBean.productLineId = this.line_value.productLineId; // 产品线
		this.productQBean.productGroupId = this.group_value.groupId; // 产品组
		this.productQBean.productTemplateId = this.product_base_value.tplId;
		this.productManageHttpService.findAvailableProductList(this.productQBean).subscribe(data => {
			if (data.returnCode.code === 'success') {
				this.cars_2 = data.resultList;
				this.total = data.total;
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
		});
	}
	// 详情
	basicDetails(item) {
		this.display = true;
		this.headerTitle = '可售产品详情';
		this.showModel = '1';
		this.updateValue = item.productId;
	}
	detailsCall(param) {
		this.display = param;
	}
	// 删除
	deleteClick(item) {
		this.confirmationService.confirm({
			message: '确认要删除吗?',
			accept: () => {
				//条件
				let param = { productId: item.productId };
				this.productManageHttpService.deleteAvailableProduct(param).subscribe(data => {
					if (data.returnCode.code == "success") {
						this.msgs = [];
						this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
						this.first = 0;
						this.searchList();
					} else {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
					}
				}, error => {
					this.msgs = [];
					this.msgs.push({ severity: 'error', summary: '提示', detail: "删除失败" });
				})
			}
		});
	}
	//分页
	paginate(event) {
		//每页显示的条数
		this.productQBean.pageSize = event.rows;
		//当前页
		let currentPage = event.page + 1;
		this.productQBean.pageNum = currentPage;
		this.first = (currentPage - 1) * this.productQBean.pageSize;
		//调用查询的方法
		this.searchList();
		this.productQBean.pageNum = 1;
	}
	// 产品线下拉
	line_query() {
		this.productQBean = new ProductQBean();
		this.productManageHttpService.productCategoryLineQueryList(this.productQBean).subscribe(data => {
			if (data.returnCode.code == "success") {
				data.productLineList.forEach(item => {
					let obj = {};
					obj['label'] = item.productLineName;
					obj['value'] = item;
					this.select_line.push(obj)
				});
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
		})
	}


	// 产品组修改
	lineVelue() {
		console.log(this.line_value)

		this.select_group = [
			{ label: '请先选择产品线', value: "" },
		];
		this.group_value = "";
		this.product_base_value = "";
		this.group_query()
	}
	// 产品组下拉
	group_query() {
		let parm = { productLineId: this.line_value.productLineId }
		this.productManageHttpService.productCategoryGroupInfoQueryList(parm).subscribe(data => {
			if (data.returnCode.code == "success") {
				data.productGroupInfoList.forEach(item => {
					let obj = {};
					obj['label'] = item.groupName;
					obj['value'] = item;
					this.select_group.push(obj)
				});
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
		})
	}
	// 产品组修改
	groupChange() {
		console.log(this.group_value);
		this.product_base_value = "";
		//查询基础产品id
		let params = {
			groupId: this.group_value.groupId,
			statusFlag: 'Y'
		}
		this.product_base_arr = [
			{ label: '请先选择产品组', value: "" },
		];
		this.productManageHttpService.pdGroupTemplateQuery(params).subscribe(data => {
			if (data.returnCode.code == "success") {
				let arr = [];
				data.groupTempList.forEach(item => {
					let obj = {};
					obj['label'] = item.tplName;
					obj['value'] = item;
					this.product_base_arr.push(obj)
				});
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
		})
	}
	// 重置
	resetClick() {
		this.line_value = '';
		this.group_value = '';
		this.product_base_value = '';
		this.select_group = [{ label: '请先选择产品线', value: "" }];
		this.product_base_arr = [{ label: '请先选择产品组', value: "" }];
		this.productQBean = new ProductQBean();
	}
	//按钮权限
	permissionCheck(id) {
		return this.commfunc.permissionCheck(id)
	}
	serveClick(item) {
		if (this.isdisabled == true) {
			this.isdisabled = false;
			this.confirmationService.confirm({
				message: '确认要提交吗?',
				accept: () => {
					let pbj = {
						productId: item.productId,
					}
					this.productManageHttpService.startWorkFlowLaunchForProduct(pbj).subscribe(data => {
						if (data.returnCode.code == "success") {
							this.searchList();
							this.isdisabled = true;
							this.msgs = [];
							this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
						} else {
							this.isdisabled = true;
							this.msgs = [];
							this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
						}
					}, error => {
						this.isdisabled = true;
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
					})
				},
				reject: () => {
					this.isdisabled = true;
				}
			});
		}
	}
	// 状态调整
	adjust(item) {
		this.isdisabled = false;
		this.confirmationService.confirm({
			message: '确认要调整状态吗?',
			accept: () => {
				let dataA = {
					productId: item.productId,
					productType: 'SALE_PRODUCT',
					statusId: '06' 
				}
				this.productManageHttpService.updateProductStatus(dataA).subscribe(data => {
					if (data.returnCode.code == "success") {
						this.searchList();
						this.isdisabled = true;
						this.msgs = [];
						this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
					} else {
						this.isdisabled = true;
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
					}
				}, error => {
					this.isdisabled = true;
					this.msgs = [];
					this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
				})
			},
			reject: () => {
				this.isdisabled = true;
			}
		});
	}
}

