import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { Message } from 'primeng/primeng';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { ActivatedRoute, Router } from '@angular/router';
import { ProductManageHttpService } from '../../http/product-manage.http.service';
import * as _ from 'lodash';

@Component({
  selector: 'app-display-properties-config',
  templateUrl: './display-properties-config.component.html',
  styleUrls: ['./display-properties-config.component.scss'],
  providers: [ProductManageHttpService]
})
export class DisplaypropertiesConfigComponent implements OnInit, OnChanges {
  item: any;
  vlaueOpen: string = 'open';
  vlaue1: any = 0;
  activeIndex: any = 0;
  items: { label: string; command: (event: any) => void; }[];
  tabMesg: any = tableMessage; // 列表无数据
  @Input() inValue: any;

  @Output() outValue: EventEmitter<any> = new EventEmitter();
  cars: any = [];
  // 模态框
  showModel: string;
  display: boolean = false;
  // 模态框的标题
  headerTitle: string;
  updateValue: any = [];
  // 提示信息
  msgs: Message[] = [];
  lists: any = [];
  list: any = [];
  listes: any = [];
  show: any;
  productFeatureCategoryId: string;
  listValue: any = [];
  iscompele: boolean = false;//完成按钮
  constructor(public productManageHttpService: ProductManageHttpService,
    public router: Router,
    public routerA: ActivatedRoute, ) {

  }
  // 分页
  pageSize: number = 10;
  pageNum: number = 1;
  total: string;

  ngOnInit() {
    this.items = [{
      label: '展示设置',
      command: (event: any) => {
        this.activeIndex = 0;
      }
    },
    {
      label: '排序设置',
      command: (event: any) => {
        this.activeIndex = 1;
      }
    }];
    this.routerA.params.subscribe((data) => {
      this.lists = {
        channelId: data.channelId,
        channelName: data.channelName,
        channelDes: data.channelDes,
        productId: data.productId,
        productName: data.productName,
        productLineName: data.productLineName,
        productGroupName: data.productGroupName,
        BaseproductName: data.BaseproductName,
        description: data.description,
      };
    });
    this.productFeatureCategory();
  }

  ngOnChanges() {

  }
  next() {
    let kk = document.getElementById('KK');
    let contents = kk.getElementsByClassName('ui-steps-number');
    for (let i = 0; i < contents.length; i++) {
      contents[i]['style'].backgroundColor = '#757575';
      if (i <= this.activeIndex) {
        contents[i]['style'].backgroundColor = '#71d071';
      }
      if (i == (this.activeIndex + 1)) {
        contents[i]['style'].backgroundColor = '#ffc107';
      }
    }
    if (this.activeIndex === 0) {
      this.listValue = [];
      this.list.forEach(items => {
        items.productFeatureTypeList.forEach(item => {
          if (item.isShow === 'true') {
            if (item['productFeatureTypeNum']) {

            } else {
              item['productFeatureTypeNum'] = '';
            }
            this.listValue.push(item);
          }
        });
      });
      this.listValue = _.clone(this.listValue);
      this.activeIndex = 1;
      this.vlaue1 = 1;
    }
  }
  prev() {
    let kk = document.getElementById('KK');
    let contents = kk.getElementsByClassName('ui-steps-number');
    for (let i = 0; i < contents.length; i++) {
      contents[i]['style'].backgroundColor = '#757575';
      if (i < this.activeIndex - 1) {
        contents[i]['style'].backgroundColor = '#71d071';
      }
      if (i == (this.activeIndex - 1)) {
        contents[i]['style'].backgroundColor = '#ffc107';
      }
    }
    if (this.activeIndex === 1) {
      this.activeIndex = 0;
      this.vlaue1 = 0;
    }
  }
  // 产品下组件以及属性的详情查询
  productFeatureCategory() {
    let param = {
      channelId: this.lists.channelId,
      productId: this.lists.productId,
    };
    this.productManageHttpService.getAvailableProductTZB(param).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.item = data.availableProduct;
        this.list = data.productFeatureCategoryList;
        this.list.forEach(items => {
          items.productFeatureTypeList.forEach(item => {
            item.isShow = item.isShow.toString();
          });
        });
        this.list = _.clone(this.list);
        this.resultClick(0, data.productFeatureCategoryList[0]);
        this.listes = _.clone(this.listes);
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    });
  }
  allServe() {
    this.iscompele = true;
    let productFeatureTypeList = [];
    this.listValue.forEach(items => {
        productFeatureTypeList.push({
          productFeatureTypeId: items.productFeatureTypeId,
          productFeatureTypeName: items.productFeatureTypeName,
          productFeatureTypeDesc: items.productFeatureTypeDesc,
          productFeatureTypeNum: items.productFeatureTypeNum,
    });
    });
      let param = {
        channelId: this.lists.channelId,
        productId: this.lists.productId,
        productFeatureTypeList: productFeatureTypeList,
      };
      this.productManageHttpService.modifyProductAttrConfigDetail(param).subscribe(data => {
        if (data.returnCode.code == "success") {
          let this_ = this;
          this.msgs = [];
          this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
          setTimeout(function () {
            this_.router.navigate(['/pages/tzb/product-manage/available-product/display-properties'])
          }, 500);
        } else {
          this.iscompele = false;
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
        }
      }, error => {
        this.iscompele = false;
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
      });
  }
  // 点击组件属性查看
  resultClick(i, col) {
    this.show = col.productFeatureCategoryName;
    this.listes = col.productFeatureTypeList;
  }
  // 是否展开组件框
  openClick() {
    if (this.vlaueOpen === 'open') {
      this.vlaueOpen = 'down';
    } else {
      this.vlaueOpen = 'open';
    }
  }
  goBack() {
    window.history.go(-1);
  }
  // 分页
  paginate(event) {
    // 每页显示的条数
    this.pageSize = event.rows;
    // 当前页
    let currentPage = event.page + 1;
    this.pageNum = currentPage;
    // 调用查询的方法
    // this.queryEvent();
    this.pageNum = 1;
  }

}
