import { Component, OnInit, Input, Output, EventEmitter, OnChanges, ElementRef, AfterViewInit } from '@angular/core';
import { GlobalDirectoryBean } from './bean/global-directory-model.bean';
import { TreeNode, SelectItem } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
import { ProductManageHttpService } from '../../../http/product-manage.http.service';
import { Commfunc } from '../../../../../../@uisftech/common/service/commonFun';

@Component({
    selector: 'app-global-directory-model',
    templateUrl: './global-directory-model.component.html',
    styleUrls: ['./global-directory-model.component.scss'],
    providers: [ProductManageHttpService, Commfunc]
})
export class GlobalDirectoryModelComponent implements OnInit, AfterViewInit {
    ANSG: any;
    @Output() outValue: EventEmitter<any> = new EventEmitter();
    @Input() inValue: any;

    selectedFiles: any;
    // tslint:disable-next-line:no-inferrable-types
    // 提示信息
    msgs: Message[] = [];
    // 列表
    cars_2: any;
    treeData: any;
    listData: any = {};

    // 模态框
    showModel: string;
    headerTitle: string;
    // tslint:disable-next-line:no-inferrable-types
    display: boolean = false;
    total: string;
    // 每页多少条
    pageSize = 10;
    //  分页控制
    first = 0;
    pageNum = 1;
    tplId: any;
    productTypeId: SelectItem[];
    code: any;
    // 组件的参数
    updateValue: any = [];
    globalDirectoryBean: GlobalDirectoryBean = new GlobalDirectoryBean();
    constructor(private productManageHttpService: ProductManageHttpService,
        private commfunc: Commfunc,
        public element: ElementRef) {
        this.codeValues(); // 调用方法，获取全部码值
        this.productTypeId = this.code['ProductTypeId'];
    }

    ngOnInit() {
        // this.productTypeId = [
        //     { label: '凭证', value: 'VOUCHER_GOOD' },
        //     { label: '可售产品', value: 'DIGITAL_GOOD' },
        //     { label: '组合产品模板', value: 'COMBIN-T' },
        //     { label: '组合产品', value: 'COMBIN-P' },
        //     { label: '基础产品', value: 'SELL_PRODUCT' },
        // ];
        this.query_line();
    }
    // 码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret; // code是自己声明的变量
    }
    ngAfterViewInit() {
        // 隐藏tree内部自带的滚动条
        // this.element.nativeElement.querySelector('.ui-tree-container').style.overflow = 'unset';
    }
    query_line() {
        this.productManageHttpService.productCategoryLineQueryList(this.globalDirectoryBean).subscribe(data => {
            if (data.returnCode.code === 'success') {
                const tempArr = [];
                data.productLineList.forEach(item => {
                    const tempObj = { id: 'line' };
                    tempObj['label'] = item['productLineName'];
                    tempObj['value'] = item;
                    tempObj['children'] = [{}];
                    tempArr.push(tempObj);
                });
                this.treeData = tempArr;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        });
    }
    nodeCheck(event) {
        this.ANSG = event.node.id;
        if (event.node.id === 'line') {
            this.listData['productlineId'] = event.node.value.productLineId;
            this.listData['productgroupId'] = '';
            this.listData['productId'] = '';
            this.listData['productName'] = event.node.value.productLineName;
            this.listData['productTypeId'] = '产品线';
            this.listData['description'] = event.node.value.productLineStatusName;
        }
        if (event.node.id === 'group') {
            this.listData['productgroupId'] = event.node.value.groupId;
            this.listData['productId'] = '';
            this.listData['productName'] = event.node.value.groupName;
            this.listData['productTypeId'] = '产品组';
            this.listData['description'] = event.node.value.groupDsc;
        }
        if (event.node.id === 'jichu') {
            this.first = 0;
            this.pageNum = 1;
            this.pageSize = 10;
            this.tplId = event.node.value.tplId;
            this.searchList();
            const params = {
                productId: event.node.value.tplId
            };
            this.productManageHttpService.getBaseProduct(params).subscribe(data => {
                if (data.returnCode.code === 'success') {
                    if (data.resultList[0] !== undefined && data.resultList[0] !== '') {
                        this.listData = data.resultList[0];
                    }
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
            });
        }
    }
    paginate(event) {
        // 每页显示的条数
        this.pageSize = Number(event.rows);
        // 当前页
        const currentPage = event.page + 1;
        this.pageNum = currentPage;
        this.first = (currentPage - 1) * this.pageSize;
        // 调用查询的方法
        this.searchList();
        this.pageNum = 1;
    }
    searchList() {
        const param = {
            productTemplateId: this.tplId,
            pageNum: this.pageNum,
            pageSize: this.pageSize
        };
        const arr = [];
        this.productManageHttpService.findAvailableProductList(param).subscribe(data => {
            if (data.returnCode.code === 'success') {
                this.cars_2 = data.resultList;
                this.total = data.total;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        });
    }
    // +号点击
    nodeExpend(event) {
        console.log(event);
        if (event.node.id === 'line') {
            this.globalDirectoryBean.productLineId = event.node.value.productLineId;
            const arr = [];
            this.productManageHttpService.productCategoryGroupInfoQueryList(this.globalDirectoryBean).subscribe(data => {
                if (data.returnCode.code === 'success') {
                    data.productGroupInfoList.forEach(item => {
                        const tempObj = { id: 'group' };
                        tempObj['label'] = item['groupName'];
                        tempObj['value'] = item;
                        tempObj['children'] = [{}];
                        tempObj['value']['productLineId'] = this.globalDirectoryBean.productLineId;
                        tempObj['value']['groupId'] = item['groupId'];
                        tempObj['value']['groupName'] = item['groupName'];
                        tempObj['value']['groupDsc'] = item['description'];
                        arr.push(tempObj);
                    });
                    event.node.children = arr;
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
            });
        }
        if (event.node.id === 'group') {
            const param = {
                groupId: event.node.value.groupId,
                // statusFlag: 'Y' // 只查询基础产品
            };
            const arr = [];
            this.productManageHttpService.pdGroupTemplateQuery(param).subscribe(data => {
                if (data.returnCode.code === 'success') {
                    data.groupTempList.forEach(item => {
                        const tempObj = { id: 'jichu' };
                        tempObj['label'] = item.tplName;
                        tempObj['value'] = item;
                        tempObj['children'] = [];
                        arr.push(tempObj);
                    });
                    event.node.children = arr;
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
            });
        }
        if (event.node.id === 'jichu') {
            const arr = [];
            event.node.children = arr;
        }
    }

    serve() {
        // 是否审批
        const paramse = {
            productId: this.listData.productId,
            flag: 'Y',
        };
        // this.productManageHttpService.productWorkFlowCheck(paramse).subscribe(data => {
        //     console.log('AAAAAAAA');
        //     if (data.returnCode.code === 'success') {
        //         if (data.workFlowFlag == 'Y' || data.productFlag == 'Y' || data.productStatusFlag == 'Y') {

        //             this.msgs = [];
        //             this.msgs.push({ severity: 'info', summary: '提示', detail: '此基础产品已有工作流或已被拒绝，无法引用！' });
        //         } else {
        // 引用
        if (this.listData.productId) {
            const parm = {
                display: false,
                list: this.listData
            };
            this.outValue.emit(parm);
        } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择基础产品！' });
        }
        //         }
        //     } else {
        //         this.msgs = [];
        //         this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
        //     }
        // }, error => {
        //     this.msgs = [];
        //     this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务错误' });
        // })
    }
}

